/* -- General App -- */

.App {
  background-color: #f7f7f7;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #b7b0b0;
  height: 8vh;
  color: white;
  /*display: flex;*/
  /*align-items: center;*/
  padding: 10px 0 10px 0;
  width: 100vw;
}

.App-title {
  font-family: IBMPlexSans-Light, ibm-plex-sans;
  font-size: 2rem;
  margin :0;
  font-weight: bold;
}

/* -- Header -- */

.header-img{
  width: auto;
  margin-left: 5%;
  max-height: 100%;
  /*height: 8vh;*/
  margin: auto 5% auto 5%;
}

/* -- Common -- */

.right {
  float: right;
}

.logo {
  width: auto;
  max-height: 100%;
  padding: 2%;
  height: 80%;
}
.header-menu {
  height: 80%;
  max-height: 100%;
  margin-right: 7%;
  display: -ms-flexbox;
  display: flex;
}

.map-container{
  width: 90%;
  height: 40vh;
  margin: auto;
  margin-top: 4vh;
  -webkit-box-shadow: 3px 3px 7px -2px rgba(0,0,0,0.75);
  box-shadow: 3px 3px 7px -2px rgba(0,0,0,0.75);
}

.map-header, .store-map-header{
  height: 15%;
  background-color: white;
  color: #626366;
  font-size: 1.2rem;
}

.card-title{
  height: 25%;
  background-color: white;
  color: #626366;
  font-size: 1rem;
  font-weight: bold;
}

.card-content{
  height: 75%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.card-left-content, .card-right-content{
  height: 100%;
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.card-left-content-iot {
  height: 100%;
  width: 70%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.card-right-content-iot {
  height: 100%;
  width: 30%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.card-data {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 1rem;
  color: #626366;
}

.card-data-value {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.smaller {
  font-size: 0.7rem;
}

.card-info-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #757679;
  font-weight: bold;
  margin-top: 1%;
}

.temp-img {
  max-height: 80%;
  max-width: 100%;
  height: 100%;
  width: auto;
}

.map-header span, .store-map-header span, .card-title span{
  height: 100%;
  margin-left: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-weight: bold;
}

.map {
  display: inline;
}
.plan{
  height: 100%;
  position: relative;
  -ms-flex-direction: column;
      flex-direction: column;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 3px 3px 1px -2px rgba(0,0,0,1);
  box-shadow: 3px 3px 1px -2px rgba(0,0,0,1);
}

.plan .temp-img-plan-main{
  height: 85%;
}

.temp-img-plan-1, .temp-img-plan-2{
  position: absolute;
  cursor: pointer;
}

.temp-img-plan-1{
  height: 10%;
  top: 140px;
  left: 150px;
}

.temp-img-plan-2 {
  height: 10%;
  top: 140px;
  left: 170px;
}

.first-column, .second-column{
  width: 24%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1 1;
      flex: 1 1;
  margin-left: 30px;
}

.center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  margin: auto;

}

.div-display{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  width: 90%;
  margin: auto;
  margin-top: 4vh;
  height: 38vh;
}
.temp-display{
  font-size: 30px;
}
.font-display{
  text-align: center;
  font-size: 0.7rem;
}

.red {
  color: #c71810;
  font-weight: bold;
}

.lines {
  height: 77%;
}
.card {
  /* Add shadows to create the "card" effect */
  -webkit-box-shadow: 3px 3px 1px -2px rgba(0,0,0,0.75);
  box-shadow: 3px 3px 1px -2px rgba(0,0,0,0.75);
  background-color: white;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  font-size: larger;
  width: 100%;
  height: 45%;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 10px 0;
}
.number-display {
  /*color: #c71810;*/
  display: -ms-flexbox;
  display: flex;
  font-weight: bold;
}

.actual-map{
  width: 50%;
}

.notif{
  text-align: center;
  font-size: 0.7rem;
}
.number-font-display{
  font-size: 15px;
}
/* On mouse-over, add a deeper shadow */
.card:hover {
  -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
          box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  
}

/* Add some padding inside the card container */
.container {
  padding: 2px 16px;
}

.App-intro {
  font-size: large;
}
.bx--modal-container{
  background-color:grey;
}
.bx--modal-container{
  border-top: 	#3d70b2;
}


@media (max-width: 1000px) {
  .div-display{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    height: 200vh;
  }

  .store-map-header{
    height: 10vh;
  }

  .plan{
    width: 90vw;
    height: 90vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex: 1 1;
        flex: 1 1;
  }

  .first-column, .second-column{
    width: 100%;
    height: 80vh;
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 30px 0;
  }

  .card{
    height: 25vh;
    margin: 30px 0;
  }
}


agm-map{ height: 800px; width: 100% ; }
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


